// Fuentes
$fuentePrincipal: 'Montserrat', sans-serif;
$fuenteSecundaria: 'Alkatra', cursive;

// Colores
$rojo: #F34F51;
$verde: #0098AE;
$oscuro: #000;
$gris: #979797;
$grisClaro: #f8f8f8;

// Asignar los nuevos colores a bootstrap
$primary: $rojo;
$secondary: $verde;
$dark: $oscuro;
$light: $gris;

// Tipografa
$font-family-sans-serif: $fuentePrincipal;


// Importar Bootstrap
@import '../../node_modules/bootstrap/scss/bootstrap.scss';