@use 'base/variables' as v;
@use 'base/mixins' as m;

.gradiente {
    background: rgb(214,245,249);
    background: linear-gradient(90deg, rgba(214,245,249,1) 0%, rgba(247,224,216,1) 100%);
}

.header {


    .titulo {
        font-size: 3rem;
        cursor: pointer;
        margin: 0 auto;
        display: block;
        width: 25rem;
    }

    .secundaria {
        font-family: v.$fuenteSecundaria;
    }


}